/**
 *
 * Customs Styles
 *
 */

*, :after, :before {
  box-sizing: border-box;
  border: 0 solid #e2e8f0;
}
body {
  background-color: #eee;
  color: #1f2028;
  font-family: "Rubik",sans-serif;
}
img {
  max-width: 100%;
  height: auto;
}
h1,
h2,
h3,
h4,
p {
  font-weight: inherit;
}
.card {
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
  border-radius: 15px;
  padding: 0.75rem 1rem;
  margin-bottom: 0.8rem;
}
.card_number {
  margin: 0;
  margin-bottom: 5px;
}
.card_description {
  margin: 0;
  color: #660033;
}

.main-container {
  min-height: 100vh;
  padding: 1rem;
  justify-content: center;
  align-items: center;
  display: flex;
}
.content {
  width: 85%;
}

.header {
  padding: .75rem;
  margin-bottom: .25rem;
  text-align: center;
}
.header_title {
  font-size: 1.5rem;
  margin: 0;
}

.container {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1200px) {
  .container {
    display: grid;
    grid-template-columns: 50% 50%;
    grid-gap: 1rem;
  }
}

.stats_title {
  text-align: center;
}
.stats_content {
  justify-content: center;
  flex-direction: row;
  display: flex;
}
.stats_card {
  flex: 1 1;
  margin-left: 0.5rem;
  margin-right: 0.5rem;
}



.footer {
  margin-top: 0.75rem;
  font-size: 0.75rem;
  color: #4a5568;
  text-align: center;
}
.footer_content {
  margin-top: 0.5rem;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}
.footer_divider {
  padding-left: .25rem;
  padding-right: .25rem;
}
.footer_link {
  text-decoration: underline;
  color: inherit;
  background-color: transparent;
}
.footer_link:hover {
  text-decoration: none;
}

.countryselect {
  font-size: 1.25rem;
  padding: .25rem;
  width: 100%;
  border-radius: .375rem;
  margin-bottom: 0.5rem;
  background-color: #eee;
  box-shadow: 0 .5rem 1rem rgba(0,0,0,.15);
}